.crew {
    display: flex;
    border: 2px solid black;
    border-radius: 15px;
    width: 350px;
    height: 100px;
    padding: 2vh;
    margin: 2vh 2vh;
    background-color: white;
    color: black;
    transition: 0.2s;
}

.crew:hover {
    transform: scale(1.05);
    transition: 0.2s;
}

.img {
    width: 100px;
    height: 100px;
}

.img img{
    width: 100px;
    height: 100px;
}

.crew_information {
    display: flex;
    justify-content: space-between;
}

.car_information {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    text-align: left;
    padding-left: 2vh
}

.distance {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}